// 
export function sortYearMonths(date1, date2) {
  // 
  function compareYearMonth(a, b) {
    // 
    a = a.replace('-', '');
    b = b.replace('-', '');

    // 
    var aYear = parseInt(a.substring(0, 4));
    var aMonth = parseInt(a.substring(4));
    var bYear = parseInt(b.substring(0, 4));
    var bMonth = parseInt(b.substring(4));

    // 
    if (aYear !== bYear) {
      return aYear - bYear;
    } else {
      // 
      return aMonth - bMonth;
    }
  }

  // 
  return compareYearMonth(date1, date2);
}
// //
export function selectDataFromArr(returnData, zbCode, fieldKey, dbCode = 'nd', cityCode = '') {
  const filteredData = returnData.dataList.data[dbCode]
    .filter(returnDataObj => {
      // 
      if (cityCode == '') {
        return returnDataObj.code.search(zbCode) !== -1
      } else {
        return returnDataObj.code.search(zbCode) !== -1 && returnDataObj.cityCode.search(cityCode) !== -1;
      }
    })
    .sort((a, b) => {
      // 
      return sortYearMonths(a.date, b.date);
    })
    .filter((returnDataObj, index, array) => {
      // 
      let filteredData = array.filter(item => item.value !== '');

      //  0 
      let lastNonZeroIndex = filteredData.length - 1;
      for (let i = filteredData.length - 1; i >= 0; i--) {
        if (filteredData[i].value !== 0) {
          lastNonZeroIndex = i;
          break;
        }
      }

      //  0 0
      return index <= lastNonZeroIndex || returnDataObj.value !== 0;
    })
    .map(item => {
      // 
      return item[fieldKey];
    });
  return filteredData;
}

// 
// basicParams-echrtIdtitlelegendTopgridTopxAxisDataArr
// zbcodeArr [A0M020202,A0M020302]
// returnData 
export function drawCommonChart(basicParams, zbArr, returnData, cityCodeArr = []) {
  // 
  const type = basicParams.chartType;
  let cname = ''
  let name = ''
  let valueArr = []
  let seriesData = []
  if (cityCodeArr.length < 1) {
    zbArr.map(zbCode => {
      //  selectDataFromArr 
      // titlename
      cname = selectDataFromArr(returnData, zbCode, 'cname', basicParams.dbCode)[0];

      if (typeof cname !== 'string' || typeof basicParams.exceptName !== 'string') {
        cname = cname || '';
      } else {
        let resultArr = cname.split('');
        const exceptArr = basicParams.exceptName.split('');

        // exceptNameresultArr
        for (const ch of exceptArr) {
          const idx = resultArr.indexOf(ch);
          if (idx !== -1) {
            resultArr.splice(idx, 1);
          }
        }

        cname = resultArr.join('').trim();
        if (cname === '') {
          cname = '';
        }
      }

      name = cname + basicParams.unit;
      valueArr = selectDataFromArr(returnData, zbCode,  'value', basicParams.dbCode);
      const seriesJson = { name: name, type: type, data: valueArr };
      seriesData.push(seriesJson)
    })
  } else {
    // --zbArr
    cityCodeArr.map(cityCode => {
      // legentname
      const result = returnData.dataList.reg.filter(item => item.code == cityCode)[0]
      if (result == undefined) {
        // result
        name = ''
      } else {
        name = result.cname
      }
      valueArr = selectDataFromArr(returnData, zbArr[0],'value', basicParams.dbCode, cityCode)
      const seriesJson = { name: name, type: type, data: valueArr };
      seriesData.push(seriesJson)
    })
  }


  // domecharts
  var chart = echarts.init(document.getElementById(basicParams.echrtId));
  // 
  var option = {
    title: {
      text: basicParams.title,
      left: 'center',
      top: 'top',
      subtext: basicParams.subtitle,
      subtextStyle: {
        fontWeight: 'bold',
        fontSize: 13,
        lineHeight: 20,
      }
    },
    tooltip: {
      //X
      trigger: 'axis'
    },
    legend: {
      left: 'center',
      top: basicParams.legendTop || '10%'
    },
    grid: {
      left: '1%',
      right: '1%',
      top: basicParams.gridTop || '25%',
      bottom: '1%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      data: returnData.dataList.sj[basicParams.dbCode].sort()
    },
    yAxis: {
      ... (basicParams.min !== undefined && basicParams.min !== null ? { min: basicParams.min } : {}),
      ... (basicParams.max !== undefined && basicParams.max !== null ? { max: basicParams.max } : {})
    },
    series: seriesData,



  };
  console.log('',basicParams.title,seriesData)
  // 
  chart.setOption(option);
}

export function getCommonChartOption(basicParams, zbArr, returnData, cityCodeArr = []) {
  const type = basicParams.chartType;
  const unit = basicParams.unit || '';
  const seriesData = [];

  if (cityCodeArr.length === 0) {
    // 
    zbArr.forEach(zbCode => {
      let cname = selectDataFromArr(returnData, zbCode, 'cname', basicParams.dbCode)?.[0] || '';

      if (typeof cname === 'string' && typeof basicParams.exceptName === 'string') {
        const resultArr = cname.split('');
        const exceptArr = basicParams.exceptName.split('');
        exceptArr.forEach(ch => {
          const idx = resultArr.indexOf(ch);
          if (idx !== -1) resultArr.splice(idx, 1);
        });
        cname = resultArr.join('').trim() || '';
      }

      const name = cname + unit;
      const valueArr = selectDataFromArr(returnData, zbCode, 'value', basicParams.dbCode);
      console.log('',valueArr);
      seriesData.push({ name, type, data: valueArr });
    });
  } else {
    // 
    cityCodeArr.forEach(cityCode => {
      const city = returnData.dataList.reg?.find(r => r.code === cityCode);
      const name = city?.cname || '';
      const valueArr = selectDataFromArr(returnData, zbArr[0], 'value', basicParams.dbCode, cityCode);
      seriesData.push({ name, type, data: valueArr });
    });
  }

  return {
    title: {
      text: basicParams.title,
      subtext: basicParams.subtitle,
      left: 'center',
      top: 'top',
      subtextStyle: { fontWeight: 'bold', fontSize: 13, lineHeight: 20 }
    },
    tooltip: { trigger: 'axis' },
    legend: { left: 'center', top: basicParams.legendTop || '10%' },
    grid: {
      left: '1%',
      right: '1%',
      top: basicParams.gridTop || '25%',
      bottom: '1%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      data: (returnData.dataList.sj?.[basicParams.dbCode] || []).sort()
    },
    yAxis: {
      type: 'value',
      ...(basicParams.min !== undefined ? { min: basicParams.min } : {}),
      ...(basicParams.max !== undefined ? { max: basicParams.max } : {})
    },
    series: seriesData
  };
}

